import { Component, Input, AfterViewInit, NgModule } from '@angular/core';
export class AdsenseComponent implements AfterViewInit { adClient: string; adSlot: string | number; adFormat: string = 'auto'; adRegion = 'page-' + Math.floor(Math.random() * 10000) + 1;

  ngAfterViewInit() {
    // attempts to push the ad twice. Usually if one doesn't work the other
    // will depeding on if the browser has the adsense code cached
    const res = this.push();
    if (res instanceof TypeError) {
      setTimeout(() => this.push(), 20);
    }
  }
  push() {
    try {
      const adsbygoogle = window['adsbygoogle'];
      adsbygoogle.push({});
      return true;
    } catch (e) {
      return e;
    }
  }
}
export class AdsenseModule { }



